import React, { useState, useEffect } from 'react'
import { Table, Input, Button, Grid, Icon } from 'semantic-ui-react'
import _ from 'lodash';

const ItemsTable = props => {
    const [column, setColumn] = useState(null);
    const [data, setData] = useState(null);
    const [filteredData, setFilteredData] = useState(null);
    const [direction, setDirection] = useState(null);
    const [price, setPrice] = useState('');
    const [searchValue, setSearchValue] = useState('');


    const fetchData = () => {
        fetch('https://bitcoinnotifier.azurewebsites.net/api/animalcrossing/getallitems')
        .then(response => response.json())
        .then((data) => {
            console.log(data);
            setData(data);
            setFilteredData(data);
        }
        );
    }
    useEffect(() => {
        fetchData();
    }, []);
    const handleSort = (clickedColumn) => {
        if (column !== clickedColumn) {
            setColumn(clickedColumn);
            setFilteredData(_.sortBy(filteredData, [clickedColumn]));
            setDirection('ascending');
            return;
        };
        setFilteredData(filteredData.reverse());
        setDirection(direction === 'ascending' ? 'descending' : 'ascending');
    };

    const handleSearchChange = (e, { value }) => {
        setSearchValue(value);
        if (!value) setFilteredData(data);
        let numberOfMatches = 0;
        let matches = [];
        data.forEach(item => {
            if (item.name.indexOf(value.toLowerCase()) > - 1) {
                numberOfMatches += 1;
                matches.push(item);
                if (numberOfMatches >= 20) {
                    setFilteredData(matches);
                    return;
                }
            }
        })
        setFilteredData(matches);
    }
    const handlePriceChange = (e, { value }) => {
        if (!value) {
            setPrice('');
            return;
        }
        var isnum = /^\d+$/.test(value);
        if (!isnum) {
            return;
        }
        setPrice(value);

    }
    const handleDeleteClick = (itemName) => {
        return;
        fetch(`https://bitcoinnotifier.azurewebsites.net/api/animalcrossing/delete?itemName=${itemName}`)
        let newItems = data.filter(item => item.name !== itemName.toLowerCase())
        setData(newItems);
        setFilteredData(newItems);
        setSearchValue('');
        setPrice('');
    }

    const handleAddClick = () => {
        var isnum = /^\d+$/.test(price);
        if (price && searchValue && isnum) {
        fetch(`https://bitcoinnotifier.azurewebsites.net/api/animalcrossing/add?itemName=${searchValue.toLowerCase()}&price=${price}`)
        var newItems = [...data];
        newItems.push({ name: searchValue.toLowerCase(), price: price });
        setData(newItems);
        setFilteredData(newItems);
        setSearchValue('');
        setPrice('');
    }


    }

    return (
        <React.Fragment>
            <Grid stackable columns={2}>
                    <Grid.Column >
                        <Input
                            fluid

                            onChange={handleSearchChange}
                            icon={{ name: 'search' }}
                            placeholder='search'
                            value={searchValue}
                        />
                    </Grid.Column>
                    {filteredData && filteredData.length === 0 &&
                        <React.Fragment>
                            <Grid.Column >
                                <Input
                                    fluid
                                    onChange={handlePriceChange}
                                    value={price}
                                    placeholder='enter price'
                                    action
                                    onClick={handleAddClick}
                                >
                                    <input />
                                    <Button primary icon onClick={handleAddClick}><Icon name='plus' /></Button>

                                    </Input>

                                
                            </Grid.Column>
                        </React.Fragment>
                    }
            </Grid>


            {filteredData && filteredData.length > 0 &&
                <Table sortable unstackable>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell
                                sorted={column === 'name' ? direction : null}
                                onClick={() => handleSort('name')}
                            >
                                Name
                    </Table.HeaderCell>
                            <Table.HeaderCell
                                sorted={column === 'price' ? direction : null}
                                onClick={() => handleSort('price')}
                            >
                                Price
                    </Table.HeaderCell>
                            <Table.HeaderCell
                            >
                                Actions
                    </Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>
                    <Table.Body>
                        {_.map(filteredData, ({ price, name }) => (
                            <Table.Row key={name}>
                                <Table.Cell>{name}</Table.Cell>
                                <Table.Cell>{Number(price)}</Table.Cell>
                                <Table.Cell><Button color='red' size='tiny' icon onClick={() => handleDeleteClick(name)}> <Icon name='trash' /></Button></Table.Cell>
                            </Table.Row>
                        ))}
                    </Table.Body>
                </Table>
            }
            {filteredData && filteredData.length === 0 &&
                <div>No results found. Double check your spelling and then add a price!</div>
            }

</React.Fragment>

    )
}
export default ItemsTable;